body{
    background-color: #060;
    text-align: center;
    min-width: 360px;
}
.menuLista{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #060;
    
}
.menuItem {
    float: left;
} 
.menuItem a {
    display: block;
    color: #7f7;
    text-align: center;
    padding: 10px 10px;
    text-decoration: none;
}
.menuItem a:hover {
    background-color: #5f5;
    color: #353;
}
.menu{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    font-size: 1.25em;
}
.rodape{
    width: 100%;
    bottom: 0;
    left: 0;   
    font-size: 1.25em;
    background-color: #060;
    color: #AfA;
}
#home{
    padding-left: 15px;
    padding-bottom: 15px;
    padding-right: 15px;
    background-color: #5f5;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 1.5em;
    height: 100vh;
}
#home > img{
    width: 150px;
    padding: 45px;
}
h1{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 900;
}
p{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 100;
}
#sobre{
    background-color: #9f9;
    text-align: left;
    font-size: 1.5em;
    height: 100vh;
    vertical-align: middle;
    display: table-cell;
    text-align: center;
}

#sobre p{
    text-align: justify;
}
a:hover{
    background-color: #060;
    color: #8f8;
}
a:link{
    text-decoration: none;
}
#projetos{
    background-color: #AfA;
    font-size: 1.5em;
    height: 100vh;
}
#blog{
    padding: 30px;
    background-color: #AfA;
    font-size: 1.5em;
}
#blog p{
    line-height: 20px;
}
#blog ul{
    list-style: none;
    line-height: 30px;
    padding: 10px;
}
#blog h1{
    padding-top: 10px;
}
#sitetags{
    background-color: #9f9;
    font-size: 1.5em;
}
#sitetags li{
    list-style: none;
    line-height: 10px;
    padding: 10px;
}
#sitetags p{
    list-style: none;
    line-height: 10px;
    padding: 10px;
}
#poste{
    background-color: #AfA;
    font-size: 1.5em;
    text-align: left;
}
.highlight{
    white-space: pre-wrap;
    font-size: 1em;
}